@import "../../../css/module/fn";
@import "../../../css/module/color";

.mui-segmented-control {
	border: 2px solid $red;
	border-radius: 0;
	
	.mui-control-item {
		color: #f48d88;
		border-color: $red;
		background-color: $red;
		border-bottom: 2px solid $red;
		&.mui-active {
			color: #fff;
			background-color: $red;
			border-bottom: 2px solid white;
		}
	}
}

.mui-control-content {
	
	@include shadow();
	
	h1 {
		color: #888;
		background-color: #fff;
		text-align: center;
		font: {
			size: p2m(30);
			weight: 900;
		}
		height: p2m(90);
		line-height: p2m(90);
		margin: 0;
	}
	
	.mui-table-view-cell {
		//margin: 20px 0;
		background-color: #fff;
		//box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.3);
		//border-radius: 4px;
		
		> .mui-navigate-right {
			font: {
				size: p2m(26);
				weight: 600;
			}
			color: black;
			text-transform: capitalize;
			height: p2m(80);
			
			.padRed {
				margin-top: -3px;
			}
		}
		
		> .mui-collapse-content {
			padding: 0;
		}
		
		.mImg{
			margin-top: 1em;
			max-width: 100%;
			max-height: none !important;
		}
		
		.summary {
			position: relative;
			background-color: #eee;
			line-height: p2m(50);
			height: p2m(50);
			padding: 0 1em;
			color: #666;
			font-size: p2m(26);
			
			> .time {
				position: absolute;
				top: 50%;
				right: 15px;
				-webkit-transform: translateY(-50%);
				transform: translateY(-50%);
			}
		}
		
		.cont {
			@include lineClip(3);
			
			padding: 1em;
			color: #333;
			background-color: #f7f7f7;
			line-height: 2em;
			border: solid #fff;
			border-top-width: 1px;
			font-size: p2m(26);
			
			> .pScan {
				display: block;
				margin-top: 1em;
				//line-height: 3em;
				//text-align: center;
				//background-color: $red;
				//color: white;
				//font-size: p2m(28);
				//text-decoration: underline;
			}
		}
	}
}